.homePachete_container{
    width: 1318px;  
    background-color: #f0f0f0;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
}
.homePachete_container > h1{
    width: 100%;
    text-align: center;
    font-size: 50px;
    font-family: "Roboto";
    color: var(--blue);
    font-weight: 500;
    margin-bottom: 30px;
}
.homePachete_text{
    width: 100%;
    max-width: 500px;
}
.homePachete_text > p{
    font-family:"Roboto";
    max-width: 600px;
    font-size: 20px;
    letter-spacing: 0.3px;
    color: #222222;
    font-weight: 400;
}
.homePachete_text > p:first-child{
    margin-bottom: 15px;
}
.homePachete_container > a > button{
width: 350px;
max-width: 350px;
font-family: "Roboto";
color: white;
background-color: var(--blue);
padding: 15px 30px;
border: none;
font-size: 18px;
font-weight: 500;
letter-spacing: 0.1px;
border-radius: 10px;
letter-spacing: 0.3px;
box-shadow: 0 4px 8px rgba(0, 0, 0, 0.12), 0 2px 4px rgba(0, 0, 0, 0.08);
margin-top: 40px;
}
/* .homePachete_container > a > button:hover{
 background-color: #0274cb;
} */
.homePachete_cards{
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: row;
}
.homePachete_first_card{
    width: 100%;
    min-height: 410px;
    max-width: 300px;
    min-width: 300px;
    background-color: white;
    border-radius: 10px;
    padding: 2rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative;
}
.homePachete_third_card{
    width: 100%;
    height: 410px;
    max-height: 420px;
    min-width: 300px;
    background-color: white;
    border-radius: 10px;
    padding: 2rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative;
}
.homePachete_third_card{
    width: 100%;
    max-width: 300px;
}
/* second card styles */
.homePachete_second_card{
    width: 100%;
    min-height: 450px;
    max-width: 400px;
    min-width: 350px;
    /* background: linear-gradient(180deg, rgba(0, 123, 255, 1) 0%, rgba(0, 95, 255, 1) 100%); */
    background-color:var(--blue) ;
    border-radius: 10px;
    margin-top:-20px;
    z-index: 2;
    padding: 2rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative;
}
.homePachete_second_card span:first-child{
    color: white;
    font-family: "Roboto";
    font-weight: 500;
    font-size: 32px;
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}
.homePachete_second_card > .homePachetete_text,
.homePachete_second_card > .homePachetete_text > svg{
    color: white !important;
}
.homePachete_second_card > .homePachete_alege_btn{
    color: var(--blue);
    background-color: white;
    font-weight: 500;
}
.homePachete_first_card > .homePachete_alege_btn:hover,
.homePachete_third_card > .homePachete_alege_btn:hover{
    /* background-color: #0274cb; */
} 
.homePachete_second_card > .homePachete_alege_btn:hover{
    background-color: rgba(255, 255, 255, 0.9)
}
.homePachete_first_card,
.homePachete_second_card,
.homePachete_third_card{
    padding-bottom: 70px;
}
.homePachete_alege_btn{
    background-color: var(--blue);
    color: white;
    padding: 10px 35px;
    border-radius: 10px;
    font-family: "Roboto";
    position: absolute;
    bottom: 2rem;
    left: 2rem;
}

.homePachete_first_card span:first-child,
.homePachete_third_card span:first-child{
    font-family: "Roboto";
    font-weight: 500;
    font-size: 32px;
    width: 100%;
    display: flex;
    align-items: center;
    color: var(--blue);
    margin-bottom: 5px;
}
.homePachete_first_card span:first-child > svg,
.homePachete_second_card span:first-child > svg,
.homePachete_third_card span:first-child > svg{
    margin-left: 5px;
    width: 30px;
    height: 30px;
    min-width: 30px;
    min-height: 30px;
}
.homePachetete_text{
    width: 100%;
    display: flex;
    font-family: "Roboto";
    font-size: 18px;
    letter-spacing: 0.2px;
    color: #222222;
    margin-bottom: 16px;
}
.homePachetete_text > svg{
    /* color: #007BFF; */
    color: var(--blue);
    margin-right: 5px;
    margin-top: 3px;
    width: 20px;
    height: 20px;
    min-width: 20px;
    min-height: 20px;
}
/* probably useless if content is in the conter */
@media screen and (min-width:1700px){
    .homePachete_container{
        width: 1636px;
    }
}
@media screen and (max-width:1350px){
    .homePachete_container{
        width: 100vw;   
        padding: 0 2rem;
    }
}
/* up probably useless */
@media screen and (max-width: 970px){
    .homePachete_cards{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
    }
    .homePachete_first_card > span:first-child,
    .homePachete_second_card > span:first-child,
    .homePachete_third_card > span:first-child{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .homePachete_first_card {
        min-height: unset;
    }
    .homePachete_alege_btn{
        position: static;
        margin-top: 20px;
    } 
    .homePachete_first_card,
    .homePachete_second_card,
    .homePachete_third_card{
        margin: 15px 0;
        max-width: 300px;
        min-width: 300px;
        padding: 20px 15px !important;
        max-height: unset;
        height: 100%;
        padding-bottom: unset;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
    }
    .homePachete_container > a > button{
        width: 300px;
    }
}